* {
  box-sizing: border-box;
}

ul, li, p {
  margin: 0;
  padding: 0;
  list-style: none;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  min-height: 100vh;
  background-image: linear-gradient(#0cc, #f04);
  flex-direction: column;
  align-items: center;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
  display: flex;
}

.country-input {
  color: #30cfd0;
  font-family: Knewave, cursive;
}

.search-container {
  cursor: pointer;
  background: #fff;
  border-radius: 30px;
  justify-content: center;
  align-items: center;
  margin-top: 118px;
  padding: 10px 20px;
  transition: all .8s;
  display: flex;
  box-shadow: 4px 4px 6px rgba(255, 255, 255, .3), -4px -4px 6px rgba(116, 125, 136, .2), inset -4px -4px 6px rgba(255, 255, 255, .2), inset 4px 4px 6px rgba(0, 0, 0, .2);
}

#search-box:hover, #search-box:focus {
  width: 180px;
}

.country-list__item {
  align-items: center;
  margin-bottom: 10px;
  display: flex;
}

.country-list__text {
  margin-left: 10px;
  font-size: 20px;
}

.country-list__item:last-child {
  margin-bottom: 0;
}

.country-info__wrapper {
  max-width: 287px;
  border-radius: 30px;
  outline: 1px solid rgba(185, 185, 185, .1);
  margin-top: 60px;
  padding: 20px;
  box-shadow: 4px 4px 6px rgba(255, 255, 255, .3), -4px -4px 6px rgba(116, 125, 136, .2), inset -4px -4px 6px rgba(255, 255, 255, .2), inset 4px 4px 6px rgba(0, 0, 0, .2);
}

.country-info__name-wrapper {
  border-radius: 30px;
  flex-direction: column;
  align-items: center;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  box-shadow: inset 2px 2px 2px rgba(255, 255, 255, .5), inset -7px -7px 10px rgba(0, 0, 0, .1), 7px 7px 20px rgba(0, 0, 0, .1), 4px 4px 5px rgba(0, 0, 0, .1);
}

.country-info__name {
  opacity: .7;
  text-transform: uppercase;
  color: rgba(0, 0, 0, 0);
  text-shadow: 1px 1px rgba(0, 0, 0, .43);
  background: linear-gradient(to right, #30cfd0 0%, #330867 100%);
  -webkit-background-clip: text;
  background-clip: text;
  font-size: 20px;
  font-weight: 700;
}

.country-info__text {
  text-align: center;
  text-shadow: 0 4px 4px rgba(0, 0, 0, .376);
  border-radius: 30px;
  margin-bottom: -19px;
  padding: 10px;
  font-size: 18px;
  font-weight: 700;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
}

.country-info__name-wrapper:hover {
  transform: scale(1.05);
  box-shadow: -2px -2px 6px rgba(255, 255, 255, .29), 2px 2px 12px rgba(200, 216, 231, .533);
}

.text__wrapper:hover {
  text-shadow: none;
  transform: scale(1.05);
}

.country-info__img {
  opacity: .6;
  width: 100%;
  border-bottom-left-radius: 30px;
  border-bottom-right-radius: 30px;
}

.country-info__text-descritpion {
  font-weight: 400;
}

.fas {
  color: #5cbdbb;
}

.fas:hover {
  color: #3b7978;
}

.country-input {
  width: 0;
  background: none;
  border: none;
  outline: none;
  font-size: 16px;
  font-weight: 500;
  transition: all .8s;
}

.text__wrapper {
  border-radius: 30px;
  margin-top: 20px;
  margin-bottom: 20px;
  box-shadow: 4px 4px 6px rgba(255, 255, 255, .3), -4px -4px 6px rgba(116, 125, 136, .2), inset -4px -4px 6px rgba(255, 255, 255, .2), inset 4px 4px 6px rgba(0, 0, 0, .2);
}

.scale-in-ver-top {
  animation: scale-in-ver-top .5s cubic-bezier(.25, .46, .45, .94) both;
}

@keyframes scale-in-ver-top {
  0% {
    transform-origin: 100% 0;
    opacity: 1;
    transform: scaleY(0);
  }

  100% {
    transform-origin: 100% 0;
    opacity: 1;
    transform: scaleY(1);
  }
}

